<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <div class="row">
      <div class="col-12">
        <div class="card">
          <div class="card-body">
            <h4 class="card-title"></h4>
            <div class="row mt-4">
              <div class="col-sm-12 col-md-6">
                <div id="tickets-table_length" class="dataTables_length">
                  <label class="d-inline-flex align-items-center">
                    Show&nbsp;
                    <b-form-select v-model="perPage" size="sm" :options="pageOptions"></b-form-select>&nbsp;entries
                  </label>
                </div>
              </div>
              <!-- Search -->
              <div class="col-sm-12 col-md-6">
                <div id="tickets-table_filter" class="dataTables_filter text-md-right">
                  <label class="d-inline-flex align-items-center">
                    Search:
                    <b-form-input
                      v-model="filter"
                      type="search"
                      placeholder="Search..."
                      class="form-control form-control-sm ml-2"
                    ></b-form-input>
                  </label>
                </div>
              </div>
              <!-- End search -->
            </div>
            <!-- Table -->
            <div class="table-responsive mb-0" style="min-height:500px">
              <b-table
                :items="formatos"
                :fields="fields"
                striped 
                responsive="sm"
                :per-page="perPage"
                :current-page="currentPage"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                :filter="filter"
                :filter-included-fields="filterOn"
                @filtered="onFiltered"
              >
                <template v-slot:cell(GENERADA)="data">
                  {{data.item.created_at|fecha}}
                </template>
                <template v-slot:cell(TECNICO)="data">
                  {{data.item.Tecnico_athc.nombre}} {{data.item.Tecnico_athc.apellido}}
                </template>
                <template v-slot:cell(TOTAL)="data">
                  {{data.item.total | moneda}}
                </template>
                <template v-slot:cell(ESTADO)="data">
                  <b-badge variant="info" v-if="data.item.status==='Creado'">{{data.item.status}}</b-badge>
                  <b-badge variant="warning" v-if="data.item.status==='Pendiente'">{{data.item.status}}</b-badge>
                  <b-badge variant="success" v-if="data.item.status==='Aprobado'">{{data.item.status}}</b-badge>
                  <b-badge variant="danger" v-if="data.item.status==='Rechazado'">{{data.item.status}}</b-badge>
                </template>
                <template v-slot:cell(PAGADO)="data">
                  <b-badge variant="warning" v-if="data.item.status_pago==='Pendiente'">{{data.item.status_pago}}</b-badge>
                  <b-badge variant="success" v-if="data.item.status_pago==='Cancelado'">{{data.item.status_pago}} </b-badge>
                 <p v-if="data.item.status_pago==='Cancelado'"> {{data.item.updated_at  | fecha}}</p>
                </template>
                <template v-slot:cell(ABONOS)="data">
                   <button type="button" class="btn btn-success btn-sm rounded-pill" @click="abonarModal(data.item.id)"  :disabled="data.item.status==='Pendiente'||data.item.status==='Rechazado'"><i class="ri-hand-heart-fill"></i>  Abonar</button>
                </template>
                <template v-slot:cell(actions)="data">

                <b-dropdown size="sm" class="">
                  <template v-slot:button-content>
                    Action
                    <i class="mdi mdi-chevron-down"></i>
                  </template>
                    <b-dropdown-item-button @click="editMode=true;ver=false;setear(data.item.id)"> Procesar </b-dropdown-item-button>
                    <b-dropdown-item-button @click="editMode=true;ver=false;setearPago(data.item.id)"> Pagar </b-dropdown-item-button>
                    <b-dropdown-item-button @click="editMode=false;ver=true;setear(data.item.id)"> Ver </b-dropdown-item-button>
                </b-dropdown>
                </template>
              </b-table>
            </div>
            <div class="row">
              <div class="col">
                <div class="dataTables_paginate paging_simple_numbers float-right">
                  <ul class="pagination pagination-rounded mb-0">
                    <!-- pagination -->
                    <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage"></b-pagination>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
 
    </div>




    <b-modal id="modal_cancelar" false size="lg"  title="Rechazar Cuenta de cobro" hide-footer>
          <ValidationObserver ref="form">      
              <b-row>
                <b-col>
                    <div class="form-group">
                    <label>Motivo de rechazo</label>
                    <ValidationProvider name="observacion" rules="required" v-slot="{ errors }">
                            <b-form-textarea
                              id="descripcion"
                              v-model="form.observacion_administrador"
                              placeholder="Enter something..."
                              rows="3"
                              max-rows="6"
                              :disabled="ver"
                            ></b-form-textarea>
                          <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                </b-col>
              </b-row>
            
        </ValidationObserver>
        <button class="btn btn-block float-right btn-danger" @click="cancelFormato()" v-if="!ver && !editMode">Rechazar</button>
     </b-modal>







    <b-modal id="modal" false size="xl"  title="Formato de solicitud de cobro" hide-footer>
          <ValidationObserver ref="form">
                 <b-card no-body v-if="tecnico.length>0">
                  <b-row no-gutters class="align-items-center">
                    <b-col md="2">
                      <b-card-img :src="tecnico[0].imagen" class="rounded-0"></b-card-img>
                    </b-col>
                    <b-col md="10">
                      <b-card-body :title="'Técnico:'+tecnico[0].nombre+''+ tecnico[0].apellido">
                        <b-card-text style="margin:0">Tipo : {{tecnico[0].tipo_tecnico}}</b-card-text>
                        <b-card-text style="margin:0">Codigo : {{tecnico[0].codigo}}</b-card-text>
                        <b-card-text style="margin:0">Dirección : {{tecnico[0].direccion}}</b-card-text>
                        <b-card-text style="margin:0">Teléfono : {{tecnico[0].telefono}}</b-card-text>
                        <b-card-text style="margin:0">Email : {{tecnico[0].email}}</b-card-text>
                        <b-card-text style="margin:0">Banco : {{tecnico[0].nombre_cuenta}}</b-card-text>
                        <b-card-text style="margin:0">Cuenta : {{tecnico[0].cuenta}} ({{tecnico[0].tipo_cuenta}})</b-card-text>
                        <b-card-text style="margin:0">Nequi : {{tecnico[0].nequi}}</b-card-text>
                        <p class="card-text">
                        </p>
                      </b-card-body>
                    </b-col>
                  </b-row>
                </b-card>
              <b-row>    
                <table id="example2" class="table table-striped"  v-show="form.items.length>0">
                   <thead>
                     <tr>
                       <th>LLAMADA</th>
                       <th>CONSECUTIVO</th>
                       <th>CODIGO</th>
                       <th>CAJERO</th>
                       <th>DESCRIPCION DEL SERVICIO</th>
                       <th>PRECIO</th>
                       <th>Transporte</th>
                       <th>EXCEDENTE F.S.T.</th>
                     </tr>
                   </thead>
                    <tbody  v-for="(item , index) in form.items" :key="item.id">
                     <tr>
                       <td>{{item.tipo_llamada}}-{{item.llamada}}</td>
                       <td>ATH-{{item.consecutivo}}</td>
                       <td>{{item.codigo}}</td>
                       <td>{{item.terminal}} ({{item.ciudad}})</td>
                       <td>{{item.descripcion}}</td>
                        <td><input :id="index+'precio'" type="text" v-model="item.total_tecnico"  class="form-control form-control-sm"  :disabled="ver"></td>
                        <td><input :id="index+'transporte'" type="text" v-model="item.transporte"  class="form-control form-control-sm"  :disabled="ver"></td>
                        <td><input :id="index+'excedente'" type="text" v-model="item.excedente"  class="form-control form-control-sm"  :disabled="ver"></td>
                     </tr>
                     <tr > <td >Reclamo</td><td colspan="7">{{item.descripcion_reclamo}}</td></tr>
                      </tbody>
                       <tr>
                       <th>Transporte:</th>
                       <th>{{form.transporte | moneda}}</th>
                       <th>Excedente</th>
                       <th colspan="2">{{form.excedente | moneda}}
                         <p v-if="form.excedente>0">(A favor de pristel)</p>
                         <p v-else>(A favor del tecnico)</p>
                       </th>
                       <th colspan="2">Total</th>
                       <th colspan="4">{{form.total | moneda}}</th>
                     </tr>
                   
                  </table>
                <h4 class="text-right text-success">Total a pagar: <span>{{form.total_cuenta | moneda}}</span></h4>
                </b-row> 
              <div class="clearfix mb-3">
                <b-button class="float-right btn-info"  @click="sumarItems()">Sumar Items +</b-button>
              </div>
              <b-row>
                <b-col>
                    <div class="form-group">
                    <label>Observaciones</label>
                    <ValidationProvider name="descripcion" rules="required" v-slot="{ errors }">
                            <b-form-textarea
                              id="descripcion"
                              v-model="form.observacion"
                              placeholder=""
                              rows="2"
                              max-rows="6"
                              :disabled="ver"
                            ></b-form-textarea>
                          <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                </b-col>
              </b-row>
              <b-row> 
              <b-col v-if="usuarioDB.rol==='Analista'">
                <ValidationProvider name="area dependiente" rules="required" v-slot="{ errors }">
                  <label>Coordinador</label>
                      <select  v-model="form.solicitante_id" class="form-control" :disabled="ver">
                        <option value=""></option>
                        <option :value="coordinador.uid" v-if="coordinador.eid===1" v-for="coordinador in permisos_coordinadores" :key="coordinador.id">{{coordinador.user.nombre}} {{coordinador.user.apellido}}</option>
                      </select>
                    <span style="color:red">{{ errors[0] }}</span>
                </ValidationProvider>
              </b-col>
              <b-col v-if="usuarioDB.rol==='Coordinador'">
                <ValidationProvider name="area dependiente" rules="required" v-slot="{ errors }">
                  <label>Administrador</label>
                      <select  v-model="form.autorizador_id" class="form-control" :disabled="ver">
                        <option value=""></option>
                        <option :value="admin.uid" v-if="admin.eid===1" v-for="admin in permisos_administradores" :key="admin.id">{{admin.user.nombre}} {{admin.user.apellido}}</option>
                      </select>
                    <span style="color:red">{{ errors[0] }}</span>
                </ValidationProvider>
              </b-col>
            </b-row>    
          
        </ValidationObserver>

                  


     <button @click="generateReport()" class="btn btn-success my-1">Descargar PDF</button>

        <vue-html2pdf
        :show-layout="false"
        :float-layout="true"
        :enable-download="true"
        :preview-modal="true"
        :paginate-elements-by-height="1400"
        :filename="'CDC'+form.id"
        :pdf-quality="2"
        :manual-pagination="false"
        pdf-format="a4"
        pdf-orientation="landscape"
        pdf-content-width="1100px"
 
        @progress="onProgress($event)"
        @hasStartedGeneration="hasStartedGeneration()"
        @hasGenerated="hasGenerated($event)"
        ref="html2Pdf"
    >
        <section slot="pdf-content">
          <div class="p-4">
                <p class="lead">{{form.creado | fecha}}</p>
                  <h5 class="text-center">Cuenta de cobro N-{{form.id}}</h5>
                 <b-card class="my-1" no-body v-if="tecnico.length>0">
                  <b-row no-gutters class="align-items-center">
                    <b-col md="1">
                      <b-card-img :src="tecnico[0].imagen" class="rounded-0"></b-card-img>
                    </b-col>
                    <b-col md="11">
                      <b-row class="pl-3">
                        <b-col>
                            <b-card-text style="margin:0">Nombre : {{tecnico[0].nombre}} {{tecnico[0].apellido}} ({{tecnico[0].tipo_tecnico}})</b-card-text>
                            <b-card-text style="margin:0">Codigo : {{tecnico[0].codigo}}</b-card-text>
                            <b-card-text style="margin:0">Dirección : {{tecnico[0].direccion}}</b-card-text>
                            <b-card-text style="margin:0">Teléfono : {{tecnico[0].telefono}}</b-card-text>
                        </b-col>
                        <b-col>
                            <b-card-text style="margin:0">Email : {{tecnico[0].email}}</b-card-text>
                            <b-card-text style="margin:0">Banco : {{tecnico[0].nombre_cuenta}}</b-card-text>
                            <b-card-text style="margin:0">Cuenta : {{tecnico[0].cuenta}} ({{tecnico[0].tipo_cuenta}})</b-card-text>
                            <b-card-text style="margin:0">Nequi : {{tecnico[0].nequi}}</b-card-text>
                        </b-col>
                      </b-row>
                    </b-col>
                  </b-row>
                </b-card>
               <table id="example2" class="table table-striped table-sm" style="font-size:10px!important"  v-show="form.items.length>0">
                   <thead>
                     <tr>
                       <th>LLAMADA</th>
                       <th>CONSECUTIVO</th>
                       <th>CODIGO</th>
                       <th>CAJERO</th>
                       <th>FECHA</th>
                       <th>DESCRIPCION DEL SERVICIO</th>
                       <th>VALOR</th>
                       <th style="white-space: nowrap">EXCEDENTE F.S.T.</th>
                     </tr>
                   </thead>
                    <tbody>
                     <tr v-for="(item , index) in form.items" :key="item.id">
                       <td style="white-space: nowrap">{{item.tipo_llamada}}-{{item.llamada}}</td>
                       <td>ATH-{{item.consecutivo}}</td>
                       <td>{{item.codigo}}</td>
                       <td>{{item.terminal}} ({{item.ciudad}})</td>
                       <td>{{item.fecha | fecha}}</td>
                       <td>{{item.descripcion}}</td>
                       <td>{{item.total_tecnico | moneda}}</td>
                       <td>{{item.excedente | moneda}}</td>
                     </tr>
                      <tr>
                       <th>Transporte:</th>
                       <th>{{form.transporte | moneda}}</th>
                       <th>Excedente</th>
                       <th>{{form.excedente | moneda}}
                        <p v-if="form.excedente>0">(A favor de pristel)</p>
                         <p v-else>(A favor del tecnico)</p>
                       </th>
                       <th style="white-space: nowrap">Total Servicio</th>
                       <th colspan="1">{{form.total | moneda}}</th>
                     </tr>
                    </tbody>
                  </table>
                    <h4 class="text-right text-success">Total a pagar: <span>{{form.total_cuenta | moneda}}</span></h4>
          </div>

        </section>
    </vue-html2pdf>



          <div class="row"  v-if="usuarioDB.rol==='Administrador'">
               <div class="col-6">
                     <button class="btn btn-success btn-block" @click="editMode=false;aprobFormato(form.id,form.solicitante_id,form.consecutivo)"><i class="ri-check-line align-middle mr-2"></i> Aprobar</button>
               </div>
               <div class="col-6">
                     <button class="btn btn-danger btn-block" @click="editMode=false;setearCancelado(form.id,form.solicitante_id,form.consecutivo)"><i class="ri-error-warning-line align-middle mr-2"></i> Devolver</button>
               </div>
           </div>


        <button v-b-tooltip.hover title="Agregar la solicitud"   class="btn btn-block float-right btn-success my-2" @click="switchLoc" v-if="!ver && usuarioDB.rol==='Analista'">Enviar al coordinador</button>
        <button v-b-tooltip.hover title="Agregar la solicitud"   class="btn btn-block float-right btn-success my-2" @click="switchLoc" v-if="!ver && usuarioDB.rol==='Coordinador'">Enviar al administrador</button>
     </b-modal>



       <b-modal id="modal_pago" false size="md"  title="Formato de solicitud de cobro" hide-footer>
          <ValidationObserver ref="form"> 
            <b-row>
              <b-col>
                  <div class="form-group">
                    <label>Fecha del pago </label>
                    <ValidationProvider name="fecha llamada" rules="required" v-slot="{ errors }">
                      <b-form-input id="date" v-model="form.fecha_pagado"  type="datetime-local"></b-form-input>
                       <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                  </div>
                </b-col>     
              </b-row>
              <b-row>
                <b-col>
                    <div class="form-group">
                    <label>Observaciones de pago</label>
                    <ValidationProvider name="observacion" rules="required" v-slot="{ errors }">
                            <b-form-textarea
                              id="descripcion"
                              v-model="form.observacion_pagado"
                              placeholder="Enter something..."
                              rows="3"
                              max-rows="6"
                              :disabled="ver"
                            ></b-form-textarea>
                          <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                </b-col>
              </b-row>
        </ValidationObserver>
        <button class="btn btn-block float-right btn-danger" @click="PagarCuestion()">Pagar</button>
     </b-modal>
  </Layout>
</template>

<script>
import moment from 'moment';   
import VueHtml2pdf from 'vue-html2pdf'
import "vue-select/dist/vue-select.css";
import {mapState,mapMutations, mapActions} from 'vuex'
import { ValidationProvider, ValidationObserver } from "vee-validate";
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import vSelect from "vue-select";

/**
 * Dashboard component
 */
export default {
  components: {
    Layout,
    PageHeader,
    ValidationProvider,
    ValidationObserver,
    vSelect,
    VueHtml2pdf,
    moment
  },
  data() {
    return {
      title: "Administracion",
      items: [
        {
          text: "Procesos"
        },
        {
          text: "Formato de solicitud de pago",
          active: true
        }
      ],
     dropzoneOptions: {
        thumbnailWidth: 150,
        maxFilesize: 0.5,
        headers: { "My-Awesome-Header": "header value" }
      },
      ver:false,
      url:"",
      url_firma:"",
      modal: true,
      file:null,
      firma:null,
      codigo: "",
      nombre: "",
      precio: "",
      password: "",
      total_abonado: 0,
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      pageOptions: [10, 25, 50, 100],
      filter: null,
      consecutivo:"",
      filterOn: [],
      terceros: [],
      entidades: [],
      abonos: [],
      imputaciones: [],
      imputacion_id: "",
      users: [],
      sortBy: "age",
      sortDesc: false,
      fields: ["id","GENERADA","TECNICO","ESTADO","PAGADO","TOTAL","FECHA PAGO","actions"],
      formatos: [], 
      cajero: [],
      tecnico: [],
      editMode:false,
      permisos_coordinadores:[],
      permisos_administradores:[],
      form:{
          'id':'',
          'items':[],
          'id_entidad':'',
          'nombre':'',
          'valor':'',
          'transporte':'',
          'total':'',
          'total_cuenta':'',
          'excendente':'',
          'solicitante_id':'',
          'observacion_pagado':'',
          'fecha_pagado':'',
      },
      abono:{
          'id':'',
          'status_pago':'',
      }
    }
  },
  filters: {
        moneda: function (value) {
          const formatterPeso = new Intl.NumberFormat('es-CO', {
             style: 'currency',
             currency: 'COP',
             minimumFractionDigits: 0
           })
           let valueFinal = formatterPeso.format(value);
           return valueFinal
        },
        fecha: function (value) {
         moment.locale('es');
        let fechafina =moment(value).format('MMMM DD YYYY')
           return fechafina
        }
  },
  methods: {

    cargar(){
         for (let index = 0; index < this.imputaciones.length; index++) {
            if( this.imputacion_id===this.imputaciones[index].id){
              console.log(this.imputacion_id);
              console.log(this.imputaciones[index].id);
              this.form.items.push({
               nombre:this.imputaciones[index].nombre,
               codigo:this.imputaciones[index].codigo,
               descripcion:"",
               precio:"",
             });
            }
        }
      },
    eliminarItem(index){
       this.form.items.splice(index, 1);  
    },
    sumarItems(){
      this.form.transporte=0;
      this.form.excedente=0;
      this.form.total=0;
      this.form.total_cuenta=0;
      for (let index = 0; index < this.form.items.length; index++) {
        this.form.total=this.form.total+parseFloat(this.form.items[index].total_tecnico);
        this.form.transporte = this.form.transporte+parseFloat(this.form.items[index].transporte);
        this.form.excedente=this.form.excedente+parseFloat(this.form.items[index].excedente);
      }
      this.form.total_cuenta=parseFloat(this.form.total)+parseFloat(this.form.transporte)+(parseFloat(this.form.excedente)*-1);
    },
      eliminarAbono(id,formato){
        this.$swal({
          title: 'Desea eliminar este abono?',
          icon: 'question',
          iconHtml: '',
          confirmButtonText: 'Si',
          cancelButtonText: 'No',
          showCancelButton: true,
          showCloseButton: true
        }).then((result) => {
          if (result.isConfirmed) {
            this.deleteAbono(id,formato);
          }
        })
      },
    generateReport () {
        this.$refs.html2Pdf.generatePdf()
     },
    async deleteAbono(id,formato){
     let data = new FormData();
      data.append('id',id);
 
       await this.axios.post('api/ascath/delete', data, {
           headers: {
            'Content-Type': 'multipart/form-data'
           }}).then(response => {
            if (response.status==200) {
               this.$swal(
                   'Agregado exito!',
                    '',
                    'success');
               this.listarAbonos(formato);
               ///limpiar el formulario
              }
            }).catch(e => {
              this.$swal('ocurrio un error!','','warning');
          });
      },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }, 

    ...mapActions(['guardarUsuario']),
    switchLoc(){
      if (this.usuarioDB.rol==='Analista') {
        this.$refs.form.validate().then(esValido => {
            if (esValido) {
              this.$swal({
                  title: 'Desea remitir al coordinador?',
                  icon: 'question',
                  iconHtml: '',
                  confirmButtonText: 'Si',
                  cancelButtonText: 'No',
                  showCancelButton: true,
                  showCloseButton: true
                }).then((result) => {
                  if (result.isConfirmed) {
                    this.procesarFormatoCoordinador();
                  }
                })
            } else {}
          });  
      }
      if (this.usuarioDB.rol==='Coordinador') {
         this.$refs.form.validate().then(esValido => {
            if (esValido) {
              this.$swal({
                  title: 'Desea remitir al administrador?',
                  icon: 'question',
                  iconHtml: '',
                  confirmButtonText: 'Si',
                  cancelButtonText: 'No',
                  showCancelButton: true,
                  showCloseButton: true
                }).then((result) => {
                  if (result.isConfirmed) {
                    this.procesarFormato();
                  }
                })
            } else {}
          });  
      }
    
    },
        PagarCuestion(id){
        this.$swal({
          title: 'Desea marcar como pagado a esta cuenta de cobro?',
          icon: 'question',
          iconHtml: '',
          confirmButtonText: 'Si',
          cancelButtonText: 'No',
